<script>
import vue, {the, lib, library} from "vue.min.js"
export default vue.router ({
	id: "account/login.html",
	setup: function () {
		var ready = vue.reference (true)
		/*
		if (the.account.on.line ()) {
			ready.value = null
			vue.on.progress.insert ("account:line")
			}
		vue.watch (the.account.cache.ready ("reference"), function () {
			if (the.account.on.line ()) vue.router.go ("cpanel.html")
			else ready.value = true
			vue.on.progress.delete ("account:line")
			})
		*/
		return vue.export ({ready})
		},
	method: {
		function () {},
		submit (extra, user, password) {
			user = user || vue.model ("user")
			password = password || vue.model ("password")
			var account
			if (account = the.account.login (user, password)) {
				account.then (function () {
					vue.model.delete ("account:notify/error")
					vue.router.go ("cpanel.html")
					})
				}
			else {
				vue.model ("account:notify/error", true)
				}
			},
		account_register () {
			var account = {
				doc: {id: "demo"},
				user: "demo",
				password: "demo",
				status: "active",
				access: ["demo"],
				}
			the.account.register (account).then (function (r) {
				console.log ("r", r)
				})
			},
		},
	})
</script>

<template id="account/login.html" url="/account/login" type="router">
	<media-query:progress v-if="vue.on.progress ()"/>
	<redirect v-else-if="the.account.on.line ()" url="cpanel.html"/>
	<layout class="flex flex-row" v-else-if="ready">
		<the:header slot/>
		<form:submit v-bind:enter="submit" class="flex align-item justify-item flex-grow padding-large" style="backgrounds: blue">
			<div class="x form-frame box-shadow">
				<div class="flex flex-row align-item justify-item gap-large padding-big">
					<img:logo src="96.png"/>
					<b font="family:logo">{{ the.app.name }}</b>
					<span class="font-bold-light">Administration System Login</span>
				</div>
				<form:field-set id="user-label">
					<legend>User ID</legend>
					<form:input id="user" model="user" for="user user-label" type="text" class="text-small" capitalize="off" style="background: white"/>
				</form:field-set>
				<br break/>
				<form:field-set id="password-label">
					<legend>Password</legend>
					<form:password id="password" model="password" for="password password-label"/>
				</form:field-set>
				<br break/>
				<div class="flex align-item">
					<div class="flex-grow" v-if="vue.model ('account:notify/error')" style="color: red">Access Denied</div>
					<div class="flex-grow" v-else>Hello World</div>
					<button:material icon="login" v-bind:option="{icon: {position: 'right'}}" type="submit"><b>Sign In</b></button:material>
				</div>
			</div>
		</form:submit>
		<the:footer slot/>
	</layout>
</template>

<style scoped>
.form-frame { padding: 10px; border: 1px solid gray; border-radius: 4px; }
fieldset { border: 1px solid gray; border-radius: 4px; background-color: white; }
fieldset.--focus { border: 1px solid red; }
legend { margin-left: 20px; padding: 0px 10px; padding-bottom: 0; }
input { width: 100%; padding: 10px 20px; border: none; background-color: transparent; }
</style>
